import request from '../../request'
//查找所有大队信息
export function teamsSelect(data) {
    return request({
        url: 'teams/select',
        method: 'post',
        data
    })
}
//查找所有车组信息
export function groupsSelect(data) {
    return request({
        url: 'groups/select',
        method: 'post',
        data
    })
}

//查找所有人员信息
export function personsSelect(data) {
    return request({
        url: 'persons/select',
        method: 'post',
        data
    })
}
//查找所有车组人员
export function selectByCondition(data) {
    return request({
        url: 'persons/selectByCondition',
        method: 'post',
        data,
        headers:{'content-type':'application/json'},

    })
}
//查找所有枪支信息
export function gunsSelect(data) {
    return request({
        url: 'guns/select',
        method: 'post',
        data
    })
}
//查找所有枪支信息
export function carsSelect(data) {
    return request({
        url: 'cars/select',
        method: 'post',
        data
    })
}
