import request from '../../request'
//人员列表
export function personList(data) {
    return request({
        url: '/persons/list',
        method: 'post',
        data
    })
}
//添加人员
export function personAdd(data) {
    return request({
        url: '/persons/add',
        method: 'post',
        data
    })
}
//修改人员
export function personUpdate(data) {
    return request({
        url: '/persons/update',
        method: 'post',
        data

    })
}
//删除人员
export function personDelete(data) {
    return request({
        url: '/persons/remove',
        method: 'post',
        data
    })
}
//根据人员姓名或身份证号搜索人员
export function personSearch(data) {
    return request({
        url: '/persons/query',
        method: 'post',
        data
    })
}

//绑定大队
export function bindTeam(data) {
    return request({
        url: '/persons/bindTeam',
        method: 'post', 
        data
    })
}
//解绑大队
export function unbindTeam(data) {
    return request({
        url: '/persons/unbindTeam',
        method: 'post',
        data
    })
}

//绑定rfid
export function bindRfid(data) {
    return request({
        url: '/persons/bindRfid',
        method: 'post', 
        data

    })
}
//解绑rfid
export function unbindRfid(data) {
    return request({
        url: '/persons/unbindRfid',
        method: 'post',
        data
    })
}
//绑定扫描设备
export function bindScanDevice(data) {
    return request({
        url: '/cars/bindScanDevice',
        method: 'post', 
        data
    })
}
//解绑扫描设备
export function unbindScanDevice(data) {
    return request({
        url: '/cars/unbindScanDevice',
        method: 'post',
        data
    })
}

//人员详情
export function personsDetail(data,cancelToken) {
    return request({
        url: '/persons/detail',
        method: 'post',
        data,
        cancelToken:cancelToken
    })
}
//上传人员图片
export function personsUpload(data) {
    return request({
        url: '/persons/upload',
        method: 'post',
        data
    })
}

//下载人员图片
export function personsDownload(data) {
    return request({
        url: '/noapp/file/photos/download',
        method: 'post',
        data,
        headers:{'content-type':'image/png'},
        responseType: 'blob',
    })
}

//添加WORKTYPE（编辑工作人员信息时用到）
export function addWorkTypes(data) {
    return request({
        url: '/worktype/addWorkTypes',
        method: 'post',
        data
    })
}

//删除人员workType
export function removeWorkType(data) {
    return request({
        url: '/worktype/removeWorkType',
        method: 'post',
        data
    })
}



