import request from '../../request'

//rfid查询，带分页功能
export function queryR(data,cancelToken) {
    return request({
        url: '/eoms/rfidInfos/query',
        method: 'post',
        data,
        headers: {
            'content-type': 'application/json'
        },
        cancelToken:cancelToken


    })
}
//rfid列表分页
export function queryRpage(data) {
    return request({
        url: '/eoms/rfidInfos/page',
        method: 'post',
        data,
        headers: {
            'content-type': 'application/json'
        },



    })
}

//更新rfid
export function updateR(data) {
    return request({
        url: '/eoms/rfidInfos/update',
        method: 'post',
        data
    })  
}
//注册rfid元数据
export function metaData(data) {
    return request({
        url: '/noapp/scanner/rfid/metaData',
        method: 'post',
        data,
        headers: {
            'content-type': 'application/json'
        },
    })  
}
//注册rfid
export function rfidReg(data) {
    return request({
        url: '/noapp/scanner/rfid/reg',
        method: 'post',
        data,
        headers: {
            'content-type': 'application/json'
        },
    })  
}

